.wrapper {
    //var
    --search-border-radius: 92px;
    --search-height: 46px;
    --search-top-spacer: 9px;
    --search-button-width: 52px;
    width: 100%;
    height: var(--default-layout-header-height);
    box-shadow: 0px 1px 1px rgb(0 0 0 / 12%);
    display: flex;
    justify-content: center;
}
.inner {
    height: 100%;
    width: var(--default-layout-width);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 24px;
}
// area menu
.more-btn {
    font-size: 2rem;
    margin-left: 28px;
    padding: 4px 8px;
    background-color: transparent;
    cursor: pointer;
}

.actions {
    display: flex;
    align-items: center;
}
.action-btn {
    font-size: 2.2rem;
    color: #161823;
    background-color: transparent;
    padding: 4px 8px;
    cursor: pointer;
    position: relative;
}
.user-avatar {
    width: 32px;
    height: 32px;
    object-fit: cover;
    border-radius: 50%;
    margin-left: 12px;
    cursor: pointer;
}

//count message
.bagde {
    position: absolute;
    padding: 0px 6px;
    font-size: 1.4rem;
    color: var(--white);
    font-weight: 600;
    top: -3px;
    right: -3px;
    line-height: 2rem;
    height: 2rem;
    background-color: var(--primary);
    border-radius: 10px;
    font-family: 'ProximaNova', sans-serif;
}
